<template>
    <div class="deviceManagement">
        <div class="device-left">
            <div class="deviceStatus"></div>
            <div class="statusMap" >
                <span style="display:inline-block;width:100px;position:relative;right:70px;margin:10px"></span> 
                <span style="color:#0094DA;position:relative;right:78px">{{deviceStatus.deviceName}}</span>
                <!-- <my-chart></my-chart> -->
                <pro-gress :deviceStatus="deviceStatus"></pro-gress>
            </div>
        </div>
        <!-- table -->
        <div class="device-right">
            <section>
                <el-row>
                <el-col :span="24">
                    
                    <!---->
                    <el-table
                    stripe
                    :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
                    border
                    style="width: 100%">
                    <!-- <el-table-column type="selection">
                    </el-table-column> -->
                    <el-table-column
                        prop="nicType"
                        label=""
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="ipAddress"
                        label="IP"
                        width="180">
                    </el-table-column>
                    <el-table-column
                        prop="subnetMask"
                        label="">
                    </el-table-column>
                    <el-table-column
                        prop="gateWay"
                        label="">
                    </el-table-column>
                    <el-table-column
                        prop="urlAddress"
                        label="URL">
                    </el-table-column>
                    <el-table-column label="" width="180">
                        <template slot-scope="scope">
                        <el-button type="primary" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                        <el-button type="danger" size="small" @click="handleDelete(scope.$index, scope.row)"></el-button>
                        </template>
                    </el-table-column>
                    </el-table>
                    <!--  -->
                    <div class="block">
                    <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pagesize"
                        layout="prev, pager, next, jumper"
                        :total="total">
                    </el-pagination>
                    </div>
                </el-col>
                </el-row>
                <el-dialog title="" :visible="dialogFormVisible" size="tiny">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="">
                    <el-input v-model="form.nicType"></el-input>
                    </el-form-item>
                    <el-form-item label="IP">
                    <el-input v-model="form.ipAddress"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                    <el-input v-model="form.subnetMask"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                    <el-input v-model="form.gateWay"></el-input>
                    </el-form-item>
                    <el-form-item label="URL">
                    <el-input v-model="form.urlAddress"></el-input>
                    </el-form-item>
                    <!-- <el-form-item label="">
                    <el-date-picker type="date" placeholder="" v-model="form.date" style="width: 100%;" ></el-date-picker>
                    </el-form-item> -->
                    <el-form-item>
                    <el-button type="primary" @click="handleSave" :loading="editLoading"></el-button>
                    <el-button @click="dialogFormVisible = false"></el-button>
                    </el-form-item>
                </el-form>
                </el-dialog>
            </section>
        </div>    
    </div>
</template>


<script>
import myChart from "./mycharts.vue"
import proGress from "./progress.vue"
const ERR_OK = "000";
  export default {
    components:{
        'my-chart':myChart,
        'pro-gress':proGress
    },
    data () {
      return {
        deviceStatus: {},
        // formInline: {
        //   user: {
        //     nicType: '',
        //     ipAddress: '',
        //     // date: '',
        //     subnetMask: [],
        //     gateWay: '',
        //     urlAddress: '',
        //     place: ''
        //   }
        // },
        tableData:[],

        options: [],
        places: [],
        dialogFormVisible: false,
        editLoading: false,
        form: {
          nicType: '',
          ipAddress: '',
          subnetMask: '',
          gateWay: '',
          urlAddress: '',
        },
        total: '',
        pagesize: 6,
        currentPage: 1,
        table_index: 999,
      };
    },
    created () {
        // console.log(mychart);
        this.$http.get('/api/product/device/manage').then((response) => {
          console.log('response:77777777',response)
          // console.log('response:77777777',response.data.result)
          // console.log('cpuUsage',response.data.result[0].deviceStatus.cpuUsage)



        this.deviceStatus = response.data.deviceStatus;
        this.tableData = response.data.deviceInfo.list;
        this.total = response.data.deviceInfo.list.length;
        console.log('tableData.............',this.tableData.list[0])

      });
    //   this.$http.get('/api/getOptions').then((response) => {
    //     response = response.data;
    //     if (response.code === ERR_OK) {
    //       this.options = response.datas;
    //       this.places = response.places;
    //     }
    //   });
    },
    methods: {
      onSubmit () {
        this.$message('~');
      },
      handleDelete (index, row) {
        this.tableData.splice(index, 1);
        this.$message({
          message: "",
          type: 'success'
        });
      },
      handleEdit (index, row) {
        this.dialogFormVisible = true;
        this.form = Object.assign({}, row);
        this.table_index = index;
      },
      handleSave () {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          cancelButtonClass: 'cancel'
        }).then(() => {
          this.editLoading = true;
        //   let date = this.form.date;
        //   if (typeof date === "object") {
        //     date = [date.getFullYear(), (date.getMonth() + 1), (date.getDate())].join('-');
        //     this.form.date = date
        //   }
//          this.tableData[this.table_index] = this.form;
          this.tableData.splice(this.table_index, 1, this.form);
          this.$message({
            message: "",
            type: 'success'
          });
          this.editLoading = false;
          this.dialogFormVisible = false;
        }).catch(() => {

        });
      },
      download: function() {
        console.log("xiazai")
        var obj = document.getElementById('download');
        var str = "IP,,,,URL\n";
        for (var i = 0; i < this.tableData.length; i++) {
          var item = this.tableData[i];
          str += item.nicType + ',' + item.ipAddress + ',' + item.date + ',' + item.subnetMask + ',' + item.gateWay + ',' + item.urlAddress;
          str += "\n";
        }
        console.log(obj)
        str = encodeURIComponent(str);
        console.log(str)
        obj.href = "data:text/csv;charset=utf-8,\ufeff" + str;
        obj.download = "download.csv";
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`: ${val}`);
      }
    }
  };
</script>

<style type="text/css">



.deviceManagement{
    width:100%;
    height: 100%;
    display: inline-block;
}
    .device-left{
        width:30%;
        height: 100%;
        display: inline-block;
        text-align: center;
    }
        .deviceStatus{
            width:100%;
            height: 60px;            
            line-height: 60px;
            background: white;
            margin-bottom: 3px;
        }
        .statusMap{
            height:552px;
            background: white;
        }
    .device-right{
        width:69%;
        height: 615px;
        background-color: white;
        /* display: inline-block; */
        float: right;
    }
    .device-right *{
        text-align: center
    }
.el-pagination {
    text-align: center;
    margin-top: 30px;
}
.el-message-box__btns .cancel {
    float: right;
    margin-left: 10px;
}
</style>


