<template>
    <div id="mychart">mychart</div>
    
</template>


<script>
import echarts from 'echarts';
    export default{
        data(){
            return {               
                option : {
                    title : {
                        text: '',
                        subtext: ''
                    },
                    tooltip : {
                        trigger: 'item'  //
                    },
                    grid:{   //
                        x:150,  //
                        y:10,   //
                        x2:30,  //
                        y2:10   //
                    },
                    xAxis : [
                        {
                            show:false,
                            type : 'value',
                            boundaryGap : [0, 0],
                            position: 'top'
                        }
                    ],
                    yAxis : [
                        {
                            type : 'category',
                            data : ['IO','','CPU',''],
                            axisLine:{show:false},     //
                            axisTick:[{    //
                                show:false
                            }],
                            axisLabel:{
                                textStyle:{
                                    fontSize:'20'
                                }
                            }
                        }
                    ],
                    series : [
                        {
                            type:'bar',
                            itemStyle:{
                                normal:{color:'rgba(0,0,0,0.5)'}
                            },
                            barGap:'-100%',
                            barCategoryGap:'43%',
                            data:[100,100,100,100],
                            animation:false
                        },
                        {
                            name:'',
                            type:'bar',
                            // stack:'',
                            tooltip:{show:false},
                            barMinHeight:30,  //
                            barWidth: 20,  //
                            barMaxWidth:100,   //
                            data:[23,29,30,10],
                            itemStyle:{
                                normal:{    //
                                    color:'#ff6600',
                                    label:{
                                        show: true,   //
                                        position: 'right',  //
                                        textStyle:{
                                            color:'#000',
                                            fontSize:'20',
                                        }
                                    },
                                    // shadowBlur: 0,
                                    // shadowOffsetX:30,
                                    // shadowOffsetY:0,
                                    // shadowColor:'rgba(20,255,255,1)',
                                    
                                    
                                },
                                
                            }
                        },
                        
                    ]
                },    
            }
        },

        computed:{
            
            
        },

        methods:{
            // showChart(){
            //     var myChart = echarts.init(document.getElementById('mychart'));
            //     myChart.setOption(this.option);
            // }
        },
        mounted(){
            var myChart = echarts.init(document.getElementById('mychart'));
            myChart.setOption(this.option);
        },
             
    }
  
</script>

<style type="text/css">
    #mychart{
        width:300px;
        height: 300px;
        /* color:rgba(20,255,255,0.5) */
    }
</style>


