<template>
    <div class = "serverManagement">
        <div>           
            <p style="margin-left:10px"></p>
            <!--  -->
            <el-dialog title="" 
            :visible.sync="dialogFormVisible1" 
            :modal-append-to-body="false" 
            width="44%" 
            :close-on-click-modal='false'
            :before-close="closeUpload">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="" >
                        <el-input v-model="formInline.taskName"></el-input>
                    </el-form-item>
                    <el-form-item label="IP" >
                        <el-input v-model="formInline.upAddress"></el-input>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input v-model="formInline.localAddress"></el-input>
                    </el-form-item>  
                    <el-form-item label="" >
                        <!-- <el-input v-model="formInline.dataCount"></el-input> -->
                        <el-radio v-model="formInline.dataCount" label=true></el-radio>
                        <el-radio v-model="formInline.dataCount" label=fasle></el-radio>
                    </el-form-item>  
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="closeUpload"> </el-button>
                    <el-button type="primary" @click="upload"> </el-button>
                </div>
            </el-dialog>
            <!--  -->
            <el-dialog title="" 
            :visible.sync="dialogFormVisible2" 
            :modal-append-to-body="false" 
            width="44%" 
            :close-on-click-modal='false'
            :before-close="closeDownload">
                <el-form :inline="true" :model="formInline2" class="demo-form-inline">
                    <el-form-item label="" >
                        <el-input v-model="formInline2.taskName"></el-input>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input v-model="formInline2.dataType"></el-input>
                    </el-form-item>
                    <el-form-item label="" >
                        <el-input v-model="formInline2.dataAddress"></el-input>
                    </el-form-item>  
                    <el-form-item label="" >
                        <el-radio v-model="formInline2.dataCount" label=true></el-radio>
                        <el-radio v-model="formInline2.dataCount" label=fasle></el-radio>
                    </el-form-item>  
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="closeDownload"> </el-button>
                    <el-button type="primary" @click="download"> </el-button>
                </div>
            </el-dialog>
        </div>
        <el-table
            :data="tableData"
            style="width: 100%">
            <el-table-column               
                type="index"
                width="80">                
            </el-table-column>
            <el-table-column               
                label=""
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column               
                label=""
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.storage }}</span>
                </template>
            </el-table-column>
            <el-table-column             
                label=""
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.allStorage }}</span>
                </template>
            </el-table-column>
            <el-table-column               
                label=""
                width="180">
                <template slot-scope="scope">
                    <span>{{ scope.row.proportion }}</span>
                </template>
            </el-table-column>
            
            <el-table-column label="">
                <template slot-scope="scope">
                    <i class="el-icon-close" 
                    size="mini"
                    @click="handleDelete(scope.$index, scope.row)"></i>
                    <i class="el-icon-edit-outline" 
                    size="mini"                   
                    @click="handleEdit(scope.$index, scope.row)"></i>
                    <el-button style="margin-left:10px" size="small" type="primary" @click="openUpload"></el-button>
                    <el-button style="margin-left:10px" size="small" type="primary" @click="openDownload"></el-button>
                </template>
            </el-table-column>
        </el-table> 
        <div class="block">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage1"
                :page-sizes="[100, 200, 300, 400]"
                :page-size="100"
                layout=" sizes, prev, pager, next, total, jumper"
                :total="400">
            </el-pagination>
        </div>       
    </div>

</template>

<script>
import Utils from "../utils/utils.js"
    export default({
        name:"data2",
        data(){
            return{
                tableData: [],
                // formInline:{
                //     taskName:"",
                //     upAddress:"",
                //     localAddress:"",
                //     dataCount: "true",
                // },
                formInline2:{
                    taskName:"",
                    dataType:"",
                    dataAddress:"",
                    dataCount: "true",
                },
                // dialogFormVisible1: false,
                // dialogFormVisible2: false,
                currentPage1: 1,
            }
        },
        computed: {
            dialogFormVisible1(){
                return this.$store.getters.uploadIsShow;
            },
            dialogFormVisible2(){
                return this.$store.getters.downloadIsShow;
            },
            formInline(){
                return this.$store.state.formInline
            }
            
        },
        created () {
            this.$http.post('/data2').then((response) => {                
                this.tableData = response.data.list2;
                console.log("3333333333",response.data)
            });
        },
        methods: {
            handleEdit(index, row) {
                console.log(index, row);
            },
            handleDelete(index, row) {
                console.log(index, row);
            },
            // 
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
            },
            
            //
            openUpload(){
                this.$store.dispatch('showDialogFormVisible1')
            },
            closeUpload(){
                this.$store.dispatch('hideDialogFormVisible1')
            },
            //
            upload(){
                var data = {}
                data.name = this.$store.state.formInline.taskName
                data.createTime = Utils.getNowFormatDate()

                               
                this.$http.post('/api/product/upload?operate=upload',data).then((response) => {             
                    this.$store.dispatch('hideDialogFormVisible1');
                    console.log("response",response) 
                    // server
                    var obj = {};
                    obj.name = this.$store.state.formInline.taskName
                    obj.type = response.data.type
                    obj.address = response.data.address
                    obj.describe = response.data.describe
                    obj.port = response.data.port
                    obj.dataDirectory = response.data.dataDirectory
                    obj.createTime = Utils.getNowFormatDate()
                    obj.startTime = '2010-04-08'
                    obj.status = ''
                    obj.startStop = ''
                    this.$store.state.tableData.push(obj)
                    console.log("response222222222",Utils.getNowFormatDate())
                });
            },
            //
            openDownload(){
                this.$store.dispatch('showDialogFormVisible2')
            },
            closeDownload(){
                this.$store.dispatch('hideDialogFormVisible2')
            },
            //
            download(){                
                this.$http.get('/api/test/download?filename=down').then((response) => {                
                    this.$store.dispatch('hideDialogFormVisible2');
                });
            },
            
        }
    })
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
p{
    height: 80px;
    margin:0;
    line-height: 80px;
}
.serverManagement{
    height: 615px;
    background-color: white
}
.block{
    text-align: center;
    margin-top:50px;
}
/* .demo-form-inline{
    text-align: center
} */
</style>



