<template>

    <div id = header>
        <div class = header-left>
            <span>MB</span>
            
        </div>
        <!--  -->
        <div class="header-right">
            <div class="header-user-con">
                <!--  -->
                <div class="btn-fullscreen" @click="handleFullScreen">
                    <el-tooltip effect="dark" :content="fullscreen?``:``" placement="bottom">
                        <i class="el-icon-rank"></i>
                    </el-tooltip>
                </div>
                <!--  -->
                <div class="btn-bell">
                    <el-tooltip effect="dark" :content="message?`${message}`:``" placement="bottom">
                        <router-link to="/tabs">
                            <i class="el-icon-bell"></i>
                        </router-link>
                    </el-tooltip>
                    <span class="btn-bell-badge" v-if="message"></span>
                </div>
                <!--  -->
                <!-- <div class="user-avator"></div> -->
                <i class="iconfont icon-yonghu1" style="font-size:25px"></i>
                <!--  -->
                <el-dropdown class="user-name" trigger="click" @command="handleCommand" style="font-size:20px;color:white">
                    <span class="el-dropdown-link">
                        {{username}} <i class="el-icon-caret-bottom"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item @click="dialogFormVisible1 = true">
                            <el-button type="text" @click="dialogFormVisible1 = true"></el-button>
                        </el-dropdown-item>

                        <el-dropdown-item @click="dialogFormVisible = true">
                            <el-button type="text" @click="dialogFormVisible = true"></el-button>
                        </el-dropdown-item>
                                               
                        <el-dropdown-item divided  command="loginout"></el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                
            </div>
        </div>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible" :modal-append-to-body="false" width="30%" :close-on-click-modal='false' >
            <el-form :model="form" :rules="rules" status-icon>
                <el-form-item label="" :label-width="formLabelWidth" prop="oldPassword">
                    <el-input v-model="form.oldPassword" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="newPassword">
                    <el-input v-model="form.newPassword" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="reNewPassword">
                    <el-input v-model="form.reNewPassword" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
            </div>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogFormVisible1" :modal-append-to-body="false" width="30%" :close-on-click-modal='false' >
            <el-form :model="form1">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="form.oldPassword" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="form.newPassword" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input v-model="form.reNewPassword" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible1 = false"> </el-button>
                <el-button type="primary" @click="dialogFormVisible1 = false"> </el-button>
            </div>
        </el-dialog>
            
    </div>
</template>


<script>
import bus from './bus';
    export default{
        data(){
            //
            let validatePass = (rule, value, callback) => {
                if(value === ""){
                    callback(new Error(""))
                }else if(value !== this.form.newPassword){
                    callback(new Error(""))
                }else{
                    callback()
                }
            }
            return {               
                collapse: false,
                fullscreen: false,
                name: '',
                message: 2,
                // 
                dialogFormVisible: false,                
                form: {
                    oldPassword: '',
                    newPassword: '',
                    reNewPassword: '',
                },
                //
                dialogFormVisible1: false,
                form1: {
                    username: '',
                    password: '',
                    rePassword: '',
                },
                formLabelWidth: '120px',

                

                rules: {
                    oldPassword: [
                        { required: true, message: '', trigger: 'blur' }
                    ],
                    newPassword: [
                        { required: true, message: '', trigger: 'blur' },
                        { type: 'string', min: 6, message: '6'}
                    ],
                    reNewPassword: [
                        { validator: validatePass, trigger: 'change'}
                    ]
                },
            }
        },

        computed:{
            username(){
                let username = localStorage.getItem('username');
                return username ? username : this.name;
            }
        },

        methods:{
            // 
            handleCommand(command) {
                if(command == 'loginout'){
                    localStorage.removeItem('ms_username')
                    this.$router.push('/');
                }
            },
            // 
            collapseChage(){
                this.collapse = !this.collapse;
                bus.$emit('collapse', this.collapse);
            },
            // 
            handleFullScreen(){
                let element = document.documentElement;
                if (this.fullscreen) {
                    if (document.exitFullscreen) {
                        document.exitFullscreen();
                    } else if (document.webkitCancelFullScreen) {
                        document.webkitCancelFullScreen();
                    } else if (document.mozCancelFullScreen) {
                        document.mozCancelFullScreen();
                    } else if (document.msExitFullscreen) {
                        document.msExitFullscreen();
                    }
                } else {
                    if (element.requestFullscreen) {
                        element.requestFullscreen();
                    } else if (element.webkitRequestFullScreen) {
                        element.webkitRequestFullScreen();
                    } else if (element.mozRequestFullScreen) {
                        element.mozRequestFullScreen();
                    } else if (element.msRequestFullscreen) {
                        // IE11
                        element.msRequestFullscreen();
                    }
                }
                this.fullscreen = !this.fullscreen;
            },
            // 
            
        },
        mounted(){
            if(document.body.clientWidth < 1500){
                this.collapseChage();
            }
            
            this.name = sessionStorage.getItem('username');
            console.log('username:',this.name)
        },
             
    }
  
</script>

<style type="text/css">
#header{
  background-color: rgba(0,148,218,1.0);
  width:100%;
  height: 50px;
  line-height: 50px;
  display: inline-block;
}
    .header-left{
        display: inline-block;
    }
    .header-right{
        position: absolute;
        right: 0;
        display: inline-block;
        height: 100%;
        margin-right: 20px;
        font-size:25px
    }
        .header-user-con div{
            display: inline-block;
            cursor: pointer;
        }

</style>


